import { useCallback, useContext, useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { debounce } from "lodash";

import { formatDate } from "@miq/utils";
import { SharedDataCtx } from "@miq/contexts";
import { AdminView, hasPerms } from "@miq/admin";
import { Icons, IconButton, Pagination } from "@miq/components";

import { docServices } from "./utils";
import DocumentAddForm from "./DocumentAddForm";

export default function DocumentListView(props) {
    const [status, setStatus] = useState("loading");

    const [data, setData] = useState({ results: [] });
    const [isAdding, setAdding] = useState(false);
    const { perms } = useContext(SharedDataCtx);

    const { search } = props.location;
    const query = new URLSearchParams(search);
    const [q, setQ] = useState(query.get("q") || "");

    const getDocuments = useCallback((...params) => docServices.list(...params), []);

    useEffect(() => {
        let params = new URLSearchParams(search);
        if (![...params.values()].filter((i) => i).length) {
            params = null;
        }

        getDocuments(params)
            .then((data) => {
                if (!data) {
                    setStatus("empty");
                } else {
                    setStatus("done");
                }

                setData(data);
            })
            .catch((err) => {
                setStatus("error");
            });
    }, [getDocuments, search]);

    const canAdd = hasPerms(perms.perms, ["miq_dms.add_document"]);

    const handleDocumentAdd = (newDocument) => {
        if (!canAdd) return;

        const url = `${props.match.path}${newDocument.slug}/`;
        return props.history.push(url);
    };

    const push = useRef(debounce((url) => props.history.push(url), 300));

    return (
        <AdminView
            title="Documents"
            className="document-list-view mb-4"
            actions={
                <>
                    <IconButton
                        labelFirst
                        Icon={Icons.Plus}
                        label="Ajouter"
                        title={
                            canAdd ? "Ajouter un nouveau document" : "Vous ne pouvez pas ajouter de nouveau document"
                        }
                        onClick={() => setAdding(!isAdding)}
                        disabled={!canAdd}
                    />
                </>
            }
        >
            <div className="miq-container">
                {isAdding ? (
                    <div className="">
                        <h3 className="mb-2">Ajouter un nouveau document</h3>
                        <DocumentAddForm onSuccess={handleDocumentAdd} onCancel={() => setAdding(false)} />
                    </div>
                ) : (
                    <div className="mb-3">
                        <input
                            required
                            type="text"
                            name="q"
                            value={q || ""}
                            onChange={(e) => {
                                const { value } = e.target;

                                setQ(value);

                                if (!value || value.length < 3) {
                                    query.delete("q");
                                } else {
                                    query.set("q", e.target.value);
                                }

                                if (!push.current) return;

                                const path = new URL(window.location.href);
                                push.current(`${path.pathname}?${query}`);
                            }}
                            className="miq-input"
                            placeholder="Chercher un document par titre, reference ou description"
                        />
                        Search tous-tous les Documents elements envoye elements recus archive
                    </div>
                )}
            </div>

            <div className="document-list mb-3">
                {data.results.map((item) => (
                    <DocumentListLink {...props} {...item} key={item.slug} />
                ))}
            </div>

            <div className="mb-4">
                <Pagination
                    {...data}
                    onPrevious={() =>
                        docServices.listPage(data.previous).then((newData) => {
                            setData(newData);
                        })
                    }
                    onNext={() =>
                        docServices.listPage(data.next).then((newData) => {
                            setData(newData);
                        })
                    }
                />
            </div>
        </AdminView>
    );
}

const DocumentListLink = (props) => {
    if (!props.slug) return null;

    const { status, is_read_only } = props;
    const isArchived = status === "ARCHIVED";

    let link = `${props.match.path}${props.slug}/`;
    if (is_read_only) link = `${link}view/`;

    return (
        <Link to={link} className="document-list-link">
            <div className="d-flex justify-content-between">
                <div className="document-title flex-1">
                    <div className="">{props.title_truncated}</div>
                    <span className="text-sm">
                        {formatDate(props.created, { month: "numeric", day: "numeric", year: "numeric" })}
                    </span>
                    {isArchived && <span className="text-sm"> - Archiv√©</span>}
                </div>

                <div className="document-meta text-muted ps-1">
                    {props.users.length > 0 && (
                        <span className="me-1 d-flex align-items-center">
                            <span className="count">{props.users.length}</span>
                            <Icons.PersonCircle />
                        </span>
                    )}

                    {props.files.length > 0 && (
                        <span className="me-1 d-flex align-items-center">
                            <span className="count">{props.files.length}</span>
                            <Icons.FileEarmarkText />
                        </span>
                    )}
                </div>
            </div>
        </Link>
    );
};
