import Form, { useForm } from "@miq/form";
import { Button } from "@miq/components";

import { docServices } from "./utils";

export default function DocumentAddForm(props) {
    const form = useForm({ title: "" });

    const handleSubmit = (e) => {
        e.preventDefault();
        const { title } = form.values;

        if (!title) {
            form.setError("title", "Entrez le titre du nouveau document");
            return;
        }

        docServices
            .post({ title })
            .then((newData) => props.onSuccess(newData))
            .catch((err) => form.handleErr(err, true));
    };

    return (
        <Form context={form} onSubmit={handleSubmit} className="document-add-form mb-3">
            <div className="mb-1">
                <Form.TextInput
                    required
                    autoComplete="off"
                    name="title"
                    placeholder="Entrez le titre du nouveau document"
                    error={form.errors.title}
                    maxLength={99}
                />
            </div>

            <div className="d-flex align-items-center justify-content-between">
                <div className="">
                    <Button onClick={props.onCancel}>Annuler</Button>
                </div>

                <div className="">
                    <Form.Submit value="Ajouter" className="btn" />
                </div>
            </div>
        </Form>
    );
}
